.file.input section.upload{position:relative;margin:25px 0 15px;border:2px dotted #d3d3d3;padding:35px 20px;border-radius:6px;display:flex;flex-direction:column;align-items:center;background-color:#fff}.file.input section.upload label{top:-21px;font-size:13px;color:#000;left:0;position:absolute}.file.input section.upload p{color:#000;font-weight:bold;letter-spacing:2.2px;margin-top:0;text-align:center}.file.input section.upload button{box-sizing:border-box;appearance:none;background-color:rgba(0,0,0,0);border:2px solid #3498db;cursor:pointer;font-size:1rem;line-height:1;padding:1.1em 2.8em;text-align:center;text-transform:uppercase;font-weight:700;border-radius:6px;color:#3498db;position:relative;overflow:hidden;z-index:1;transition:color 250ms ease-in-out;font-family:"Open Sans",sans-serif;width:45%;display:-webkit-box;display:-webkit-flex;display:-ms-flexbox;display:flex;align-items:center;padding-right:0;justify-content:center}.file.input section.upload button i{font-size:22px;margin-right:5px;border-right:2px solid;position:absolute;top:0;bottom:0;left:0;right:0;width:20%;display:-webkit-box;display:-webkit-flex;display:-ms-flexbox;display:flex;flex-direction:column;justify-content:center}.file.input section.upload button i:before{content:"📁"}.file.input section.upload input{font-size:18px;display:block;width:100%;border:none;text-transform:none;position:absolute;top:0;left:0;right:0;bottom:0;opacity:0}.file.input section.upload input:focus{outline:none}.file.input article.preview{margin-bottom:35px}.file.input article.preview section.file-list{display:flex;flex-wrap:wrap;margin-top:10px}.file.input article.preview section.file-list section.file{padding:.25rem;width:20%;height:120px;border-radius:6px;box-sizing:border-box}.file.input article.preview section.file-list section.file:hover{opacity:.55}.file.input article.preview section.file-list section.file:hover .meta-data{display:flex !important}.file.input article.preview section.file-list section.file>div:first-of-type{height:100%;position:relative}@media only screen and (max-width: 750px){.file.input article.preview section.file-list section.file{width:25%}}@media only screen and (max-width: 500px){.file.input article.preview section.file-list section.file{width:50%}}@media only screen and (max-width: 400px){.file.input article.preview section.file-list section.file{width:100%;padding:0 0 .4em}}.file.input article.preview section.file-list section.file img{border-radius:6px;width:100%;height:100%}.file.input article.preview section.file-list section.file .meta-data{display:flex;flex-direction:column;position:absolute;top:0;left:0;right:0;bottom:0;padding:10px;border-radius:6px;color:#fff;font-weight:bold;background-color:rgba(5,5,5,.55)}.file.input article.preview section.file-list section.file .meta-data[data-isimagefile=true]{display:none}.file.input article.preview section.file-list section.file .meta-data aside{margin-top:auto;display:flex;justify-content:space-between}.file.input article.preview section.file-list section.file .meta-data aside i{font-style:normal;cursor:pointer}.file.input article.preview section.file-list section.file .meta-data aside i:after{content:"🗑"}.file.input article.preview section.file-list section.file .meta-data aside i:hover{transform:scale(1.5)}